:root {
  --container-width-l: 1270px;
  --container-padding: 15px;
  --main-font: "Inter", sans-serif;
  --secondary: rgba(15, 31, 28, 0.5);
}

body {
  font-family: var(--main-font);
  background-color: #fff;
  color: #0f1f1c;
  padding-top: 50px;
}

.none {
  display: none !important;
}

.container {
  padding: 0 var(--container-padding);
  max-width: var(--container-width-l);
  margin: 0 auto;
}

.container-right {
  padding-left: calc((100% - var(--container-width-l)) / 2 + var(--container-padding));
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
}

.title {
  margin-bottom: 40px;
  font-size: 42px;
  font-weight: 700;
}

/* Grid */
.row {
  display: flex;
  column-gap: 60px;
}

.col-right {
  min-width: 400px;
  flex-basis: 30%;
}

/* Summary */
.summary {
  display: flex;
  flex-wrap: wrap;
  column-gap: 50px;
  row-gap: 30px;
}

/* Summary Item*/
.summary-item__title {
  margin-bottom: 10px;
  font-size: 14px;
  color: #888888;
}

.summary-item__value {
  font-size: 18px;
  font-weight: 600;
}

.summary-item__value--bigger {
  font-size: 24px;
  font-weight: 700;
}

.summary-item__value--percents {
  /* color: rgb(6, 132, 228); */
  color: #21bb57;
}

/* Programs */
.programms-wraper {
  display: flex;
  flex-wrap: wrap;
  column-gap: 10px;
  row-gap: 10px;
  margin-bottom: 30px;
}

/* Radio */
.radio-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
}

.radio-label {
  display: inline-block;
  padding: 0.5em 1em;
  border-radius: 6px;
  border: 1px solid #e9e9e9;
  font-size: 16px;
  font-weight: 400;
}

.radio-hidden:checked + .radio-label {
  border-color: transparent;
  background-color: #21bb57;
  color: #fff;
}

/* Params */
.params-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 30px;
}

.param__details {
  padding: 0.5em 1em;
  border-radius: 6px 6px 0px 0px;
  border: 1px solid #d3d4d4;
  border-bottom: none;
}

.param__details--error {
  border-color: rgb(233, 0, 54);
}

.param__title {
  margin-bottom: 3px;
  font-size: 14px;
  color: #888888;
}

.param__value {
  display: block;
  width: 100%;
  font-size: 18px;
}

.param__range-slider {
  margin-top: -3px;
  height: 8px;
  background-color: #21bb57;
  border-radius: 0px 0px 6px 6px;
}

.param__limits {
  padding-top: 8px;
}

.limit {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.limit__from {
  font-size: 14px;
  color: #888888;
}

.limit__to {
  font-size: 14px;
  color: #888888;
}

.limit__50 {
  position: absolute;
  border-left: 1px solid #c2c2c2;
  padding-left: 5px;
  left: 50%;
  top: 10px;
  font-size: 14px;
  color: #888888;
}

/* Sliders */
#slider-cost, #slider-downpayment, #slider-term {
  height: 8px;
}

#slider-cost, #slider-downpayment, #slider-term #slider-cost .noUi-handle,
#slider-downpayment .noUi-handle,
#slider-term .noUi-handle {
  box-shadow: none;
}

/* Hide markers on slider handles */
#slider-cost .noUi-handle::before,
#slider-cost .noUi-handle::after,
#slider-downpayment .noUi-handle::before,
#slider-downpayment .noUi-handle::after,
#slider-term .noUi-handle::before,
#slider-term .noUi-handle::after {
  display: none;
}

.noUi-connects {
  background: #fff;
}

#slider-cost .noUi-connect,
#slider-downpayment .noUi-connect,
#slider-term .noUi-connect {
  background: #21bb57;
}

#slider-cost .noUi-handle,
#slider-downpayment .noUi-handle,
#slider-term .noUi-handle {
  height: 20px;
  width: 20px;
  top: -7px;
  right: -10px; /* half the width */
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 0 1px 4px rgba(36, 38, 41, 0.24);
  cursor: pointer;
  transition: transform 0.1s ease-in;
}

#slider-cost .noUi-handle:hover,
#slider-downpayment .noUi-handle:hover,
#slider-term .noUi-handle:hover {
  transform: scale(1.3);
}